import React from "react";
import { styled } from 'styled-components';
// import Image from './image';
import Portrait from '../img/Portrait.png';
import { SimpleModal } from "./SimpleModal/SimpleModal";
// import Modal from 'react-modal';

const LecturerContainer = styled.div `  
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
` ;

const Header = styled.h2 `  
  margin-bottom: 10vw;
` ;
const Name = styled.h4 `  
  margin-bottom: 10vw;
  top: 3927px;
  font-family: 'Gogol';
  font-style: italic;
  font-weight: 400;
  font-size: 64px;
  line-height: 32px;
  color: #FFFFFF;
` ;

const ImageLecturer = styled.img `  
  height: auto;
  max-width: 500px;
` ;
const AboutMe = styled.p `  
  width: 70%;
  margin-top: 10vw;
  margin-left: -20vw;
  height: 45px;
  font-family: 'Montserrat Alternates';
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  line-height: 15px;
  color: #FFFFFF;
`;

const Button = styled.button `  
  display:block;
  margin:20px auto;
  padding:10px 20px;
  border-radius:10px;
  text-transform:uppercase;
  font-weight: bold;
  background: transparent;
  border: 1px solid #fff;
  color: #fff;
  cursor: pointer;
  transition: .5s;
  ::hover{
  background: rgba(0,0,0,0.5);
  border-color: rgba(0,0,0,0.3);
}
` ;

class Lecturer extends React.Component {
  // constructor(props) {
  //   super(props);
  //   this.state = {
  //     modalInfoIsOpen: false,
  //     modalAboutIsOpen: false,
  //     modalDataIsOpen: false,
  //   };
  // }

  // constructor(props) {
  //   super(props);
  //   this.state = {
  //     modalInfoIsOpen: false,
  //     modalAboutIsOpen: false,
  //     modalDataIsOpen: false,
  //   };
  // }
  constructor(props){
    super(props)
    this.state = {
              modalInfoIsOpen: false,
              // modalAboutIsOpen: false,
              // modalDataIsOpen: false
      };
    }
    openModal = (modalName) => {
      this.setState({ [modalName]: true });
    };
  
    closeModal = (modalName) => {
      this.setState({ [modalName]: false });
    };

  render() {
    return <LecturerContainer>
        <Header>Лектор:</Header>
        <Name>Олеся Литневски</Name>
        <ImageLecturer src={ Portrait } alt={Portrait} style={{ width: '100%' }} /> 
        <AboutMe>Еще в детстве воспитатели заметили мою любовь к импровизированным концертам и посоветовали родителям отдать меня в музыкальную школу.</AboutMe>
        <AboutMe>Музыка – это моя история, мой путь, который продолжается. </AboutMe>


<section className="section">
          <Button
            className="modal-show-button"
            onClick={() => this.openModal("modalInfoIsOpen")}
          >
            Читать дальше
          </Button>

          <SimpleModal
            isOpen={this.state.modalInfoIsOpen}
            onClose={() => this.closeModal("modalInfoIsOpen")}
          >
            <h3>О себе</h3>
            <p>
            Музыка всегда была частью моей жизни. Еще в детстве воспитатели заметили мою любовь к импровизированным концертам и посоветовали родителям отдать меня в музыкальную школу. Так начался мой долгий путь в искусстве, полный поисков, открытий и возвращений.
            Образование стало важной частью этого пути: факультет музыкальной педагогики Университета, затем юридическая академия, а позже – магистратура в Академии Музыки и Театра в Эстонии и Институте Современного Искусства в Москве. В процессе учебы я участвовала в мастер-классах, конкурсах, становилась лауреатом, посещала конференции, семинары и летние школы.
            </p>
            <p>
            Моя профессиональная деятельность включает работу учителем музыки, участие в различных музыкальных проектах и сольные выступления. Сегодня я реализую новый авторский проект концертов-лекций, в котором рассказываю о связи классической музыки с другими видами искусства, знакомлю аудиторию с выдающимися произведениями и помогаю талантливым людям раскрыть себя.
            </p>
            <p>
            Музыка – это моя история, мой путь, который продолжается. 
            </p>
          </SimpleModal>
      
        </section>
    </LecturerContainer>;
  }
}

export default Lecturer;